<template>
  <div class="Series">
    <h1>全部分类</h1>
    <!-- <div class="banner"></div> -->
    <div class="content">
      <!-- 导航 -->
      <div class="left">
        <div class="wrapper"  ref="wrapperLeft">
           <ul>
            <li
              v-for="(item, index) in list" :key="index"
              :class="index==selIndex?'sel':''"
              @click="changeSelIndex(index)"
            >
              <span>{{item.seriesName}}</span>
            </li>
          </ul>
        </div>
      </div>
      <!-- 内容 -->
      <div class="right wrapper" ref="wrapperRight">
        <div class="rightContent">
          <div class="banner2" :style="{'background-image':`url(${src})`}"></div>
          <div class="classify" v-for="(item, index) in selSeries" :key="index">
            <div class="class">
              <h2><span>{{item.classifyName}}</span><i></i></h2>
            </div>
            <div class="foodBox">
              <div class="food"
              v-for="(fItem, index) in item.list" :key="index"
               @click="addFoodData(fItem)"
              >
                <div class="img" :style="{'background-image':`url(${fItem.img})`}"></div>
                <span class="sl">{{fItem.foodName}}</span>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import Bs from 'better-scroll'
import { getAllFood } from 'api/api'
import { handleData, handleDataFood } from 'utils/handleData'
import { mapMutations } from 'vuex'
export default {
  data () {
    return {
      list: [],
      selIndex: 0,
      src: this.baseUrl + '/public/img/seb1.jpg'
    }
  },
  computed: {
    selSeries () { // 分类数据
      if (this.list.length === 0) { return false }
      const count = 3 // 每行显示个数
      const classifyList = this.list[this.selIndex].list
      const array = []
      // 菜品数据取整
      classifyList.forEach((item) => {
        const length = parseInt(item.list.length / count) * count
        item.list.length = length
        if (length > 0) {
          array.push(item)
        }
      })
      return array
    }
  },
  methods: {
    ...mapMutations(['addFoodData']),
    initBs () {
      const wrapperLeft = this.$refs.wrapperLeft
      const wrapperRight = this.$refs.wrapperRight
      /* eslint-disable no-new */
      new Bs(wrapperLeft, { click: true })
      /* eslint-disable no-new */
      new Bs(wrapperRight, { click: true })
    },
    // 获取初始化数据
    getInitData () {
      getAllFood().then((res) => {
        console.log(res)
        // 数据处理
        let data = handleDataFood(res.data, this.baseUrl)
        data = handleData(data)
        this.list = data
        this.$nextTick(() => {
          this.initBs()
        })
      })
    },
    // 导航点击切换选中项
    changeSelIndex (index) {
      this.selIndex = index
    }
  },
  mounted () {
    this.getInitData()
  }
}
</script>
<style lang="less" scoped>
@import '~style/index.less';
.Series{
  width: 7.5rem;
  position: absolute;
  top: 0;
  bottom: 0;
  display: flex;
  flex-direction: column;
  h1{
    font-size: .34rem;
    line-height: .88rem;
    text-align: center;
    color: #333;
  }
  .banner{
    background: url('/seb1.jpg') center center no-repeat;
    background-size: cover;
    padding-top: 3rem;
  }
  .content{
    flex: 1;
    overflow: hidden;
    position: relative;
    left: 0;
    right: 0;
    display: flex;
    flex-direction: row;
    border-top: 1px solid #eee;
    .left{
      width: 1.8rem;
      height: 100%;
      position: relative;
      .wrapper{
        text-align: center;
        background: #f9f9f9;
        overflow: hidden;
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        ul{
          li{
            width: 100%;
            padding: .4rem 0;
            font-size: .30rem;
            line-height: .46rem;
            color: #6f6f6f;
          }
          li.sel{
            color: @orange;
            background: #fff;
          }
        }
      }
    }
    .right{
      flex: 1;
      overflow: hidden;
      position: relative;
      top: 0;
      bottom: 0;
      .banner2{
        width: 100%;
        height: 2.4rem;
        background-size: cover;
        background-position: center center;
        margin: 0 auto .3rem;
      }
      .classify{
        text-align: center;
        h2{
          display: inline-block;
          font-size: .26rem;
          height: 0.4rem;
          line-height: 0.4rem;
          font-weight: normal;
          margin: .1rem auto;
          padding: 0 .4rem;
          color: #222;
          position: relative;
          span{
            display: inline-block;
            padding: 0 .3rem;
            background: #fff;
          }
          i{
            display: inline-block;
            width: 100%;
            height: 1px;
            background: #999;
            position: absolute;
            top: 0;
            left: 0;
            right: 0;
            bottom: 0;
            z-index: -1;
            margin: auto;
          }
        }
        .foodBox{
          display: flex;
          flex-wrap: wrap;
          justify-content: space-around;
          .food{
            width: 28%;
            margin: .2rem 0;
            display: flex;
            flex-direction: column;
            align-items: center;
            .img{
              width: 100%;
              height: 1.5rem;
              margin-bottom: .2rem;
              border-radius: .1rem;
              background-size: cover;
              background-position: center center;
            }
            span{
              font-size: .24rem;
              line-height: .3rem;
              width: 80%;
            }
          }
        }
      }
    }
  }
}
.pos{
  height: 1.5rem;
}
</style>
